import { Pipe, PipeTransform } from '@angular/core';

@Pipe({
  name: 'filterProduct'
})
export class FilterProductPipe implements PipeTransform {

  transform(arr: any, showMobile: boolean, showTablet: boolean, showCover:boolean, showGlass:boolean): any {
    let items = [];
    if(showMobile) items = [...items, ...arr.filter(x => x.type === '–ú–æ–±—ñ–ª—å–Ω–∏–π —Ç–µ–ª–µ—Ñ–æ–Ω')]
    if(showTablet) items = [...items, ...arr.filter(x => x.type === '–ü–ª–∞–Ω—à–µ—Ç')]
    if(showCover) items = [...items, ...arr.filter(x => x.type === '–ß–æ—Ö–æ–ª')]
    if(showGlass) items = [...items, ...arr.filter(x => x.type === '–ó–∞—Ö–∏—Å–Ω–µ —Å–∫–ª–æ')]
    return items;
  }

}
